import React from "react";
import { Route, Switch } from "react-router-dom";
import Loadable from "react-loadable";

import Spinner from "../shared/components/spinner";

const HomePage = Loadable({
  loader: () => import("./pages/homePage"),
  loading: Spinner
});

const ResultPage = Loadable({
  loader: () => import("./pages/resultPage"),
  loading: Spinner
});

const AddElectorPage = Loadable({
  loader: () => import("./pages/addElectorPage"),
  loading: Spinner
});

export default ({ match }) => (
  <Switch>
    <Route exact path="/" component={HomePage} />
    <Route path={`${match.url}/home`} component={HomePage} />
    <Route path={`${match.url}/result`} component={ResultPage} />
    <Route path={`${match.url}/add/elector`} component={AddElectorPage} />
  </Switch>
);
